import { Component, OnInit, ViewChild, Injectable, OnChanges, OnDestroy } from '@angular/core';
import { Message } from '../shared/message.model';
import { NgForm } from '@angular/forms';
import { MessageService } from './message.service';
import { AuthService } from '../auth/auth.service';
import { User } from '../shared/user.model';
import { Subscription } from 'rxjs';

@Component({
    selector: 'app-chat',
    templateUrl: './chat.component.html',
    styleUrls: ['./chat.component.css']
})
@Injectable()
export class ChatComponent implements OnInit, OnDestroy {
    @ViewChild('f') messageForm: NgForm;
    // messages: Message[] = [];
    conversation: Message[] = [];
    users: User[] = [];
    user_id = -1;
    user_name = 'Me';
    receiver_id = 0;
    receiver_name = '';
    userSubscription: Subscription;
    convSubscription: Subscription;

    constructor(private msgService: MessageService,
        private authService: AuthService) { }


    ngOnInit() {
        this.user_id = this.msgService.getActiveUserId();

        // Init Users
       this.userSubscription = this.msgService.getUsers()
            .subscribe((users) => {
                this.users = users;
                this.users = this.users.filter((userEl) => userEl['id'] !== this.user_id); // removing active user
                console.log('getting users from subscribtion', this.users);
                console.log('active user is: ', this.user_id);
                this.receiver_id = this.users[0]['id'];
                 this.updateConversation();
            },
                (error) => {
                    console.log(error);
                });
    }

    // When user hits send
    onSend() {
        const newMessage = new Message(0,
            this.user_id,
            this.receiver_id,
            this.messageForm.value.message,
            new Date().toString(),
        );
        // this.messages.push(newMessage);
        this.conversation.push(newMessage);
        this.msgService.saveMessage({
            user_id: this.user_id,
            receiver_id: this.receiver_id,
            content: newMessage.content
        })
            .subscribe(data => {
                console.log(data);
                this.scrollChatDown();
            });
        this.messageForm.reset();
    }

    // Update chat log depending on whom is selected
    updateConversation() {
        this.convSubscription  = this.msgService.getConversation(this.receiver_id)
            .subscribe((conversationData) => {
                this.conversation = <Message[]>conversationData;
            });
    }

    // Helper function to scroll down after new message
    scrollChatDown() {
        const objDiv = (<HTMLInputElement>document.getElementById('chatlog'));
        objDiv.scrollTop = objDiv.scrollHeight;
    }

    // Change chat log and update whom is active reciever
    onSelectChange(event: Event) {
        this.receiver_id = +(<HTMLInputElement>event.target).value;
        const selectedUsers = this.users.find(user => user['id'] === this.receiver_id);
        console.log(selectedUsers);
        this.receiver_name = selectedUsers['name'];
        this.updateConversation();
    }

    // Logout
    onLogout() {
        this.authService.logout();
    }

    // Used in template to add sender in every message
    decideSender(id: number) {
        if (id === this.user_id) {
            return this.user_name;
        } else {
            return this.receiver_name;
        }
    }

    // Destroy our subscribtions
    ngOnDestroy() {
        this.userSubscription.unsubscribe();
        this.convSubscription.unsubscribe();
    }

}
